.stage-cube-cont {
    width: 60%;
    height: 100%;
    top: 0;
    padding-top: 15%;
    margin-left: 0;
    position: absolute;
    right: 0;
    overflow: hidden;
  }
  
  .cubespinner {
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 125px 125px 0;
    margin-left: calc(50% - 100px);
  
    div {
      position: absolute;
      width: 250px;
      height: 250px;
      border: 1px solid #EEEEEE;
      background: #EEEEEE;
      opacity: 80%;
      text-align: center;
      font-size: 125px;
      display: flex;
      justify-content: center;
      align-items: center;
      box-shadow: 0 0 36px 6px #81cdd1,
          
                   ;
    }
  
    .face1 {
      transform: translateZ(125px);
      color: #f06529;
    }
    .face2 {
      transform: rotateY(90deg) translateZ(125px);
      color: #5ED4F4;
    }
    .face3 {
      transform: rotateY(90deg) rotateX(90deg) translateZ(125px);
      color: #28A4D9;
    }
    .face4 {
      transform: rotateY(180deg) rotateZ(90deg) translateZ(125px);
      color: #EFD81D;
    }
    .face5 {
      transform: rotateY(-90deg) rotateZ(90deg) translateZ(125px);
      color: #28A4D9;
    }
    .face6 {
      transform: rotateX(-90deg) translateZ(125px);
      color: #000;
    }
  }
  
  @keyframes spincube {
    from,
    to {
      transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16% {
      transform: rotateY(-90deg) rotateZ(90deg);
    }
    33% {
      transform: rotateY(-90deg) rotateX(90deg);
    }
    50% {
      transform: rotateY(-180deg) rotateZ(90deg);
    }
    66% {
      transform: rotateY(-270deg) rotateX(90deg);
    }
    83% {
      transform: rotateX(90deg);
    }
  }